.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 2rem;
  position: relative;
}

.game-title {
  font-size: 3.5rem;
  color: #ffffff;
  margin-bottom: 2rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  font-weight: bold;
}

.game-board {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
}

.cell {
  width: 120px;
  height: 120px;
  border: none;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  font-size: 3.5rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.cell:hover:not(:disabled) {
  background: #ffffff;
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.cell.O {
  color: #00bcd4;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.cell.X {
  color: #ff4081;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.winner-message {
  background: rgba(255, 255, 255, 0.95);
  padding: 2rem 4rem;
  border-radius: 15px;
  margin: 2rem 0;
  text-align: center;
  font-size: 2rem;
  color: #333;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  animation: slideIn 0.5s ease;
}

.new-game-btn, .reset-btn {
  padding: 1rem 2.5rem;
  margin: 1.5rem;
  border: none;
  border-radius: 30px;
  background: #ffffff;
  color: #764ba2;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.new-game-btn:hover, .reset-btn:hover {
  background: #f8f9fa;
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.back-btn {
  position: absolute;
  top: 20px;
  left: 20px;
  padding: 1rem 2rem;
  border: none;
  border-radius: 30px;
  background: rgba(255, 255, 255, 0.9);
  color: #764ba2;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.back-btn:hover {
  background: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

@keyframes slideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 480px) {
  .cell {
    width: 90px;
    height: 90px;
    font-size: 2.5rem;
  }

  .game-title {
    font-size: 2.5rem;
  }

  .winner-message {
    padding: 1.5rem 2rem;
    font-size: 1.5rem;
  }
}
